.projects {
    background: #f4f9ff;
    padding: 4rem 2rem;
    text-align: center;
  }
  
  .projects h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    color: #222;
  }
  
  .project-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
  }
  
  .project-card {
    background: white;
    padding: 2rem;
    border-radius: 16px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
    text-align: left;
    transition: transform 0.3s ease;
  }
  
  .project-card:hover {
    transform: translateY(-8px);
  }
  
  .project-card h3 {
    font-size: 1.4rem;
    margin-bottom: 0.5rem;
    color: #007bff;
  }
  
  .project-card p {
    font-size: 1rem;
    color: #444;
    margin-bottom: 0.8rem;
  }
  
  .project-card .tech {
    display: inline-block;
    font-size: 0.85rem;
    background: #e0f0ff;
    padding: 4px 10px;
    border-radius: 20px;
    margin-bottom: 0.8rem;
    color: #007bff;
  }
  
  .project-card a {
    color: #007bff;
    font-weight: bold;
    text-decoration: none;
  }
  
  .project-card a:hover {
    text-decoration: underline;
  }
  